.education-item-button {
  text-decoration: none;
  background-color: #f5f5f5;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.education-item-button:focus{
  outline: none;
  background-color: #032C59;
  color: white;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}


.education-item-button:hover {
  background-color: #023670;
  color: white;
}

.education-grid {
  display: grid;
  /* grid-template-columns: 1fr 1fr; */
  grid-template-rows: auto;
  grid-auto-flow: row;
  grid-gap: 2rem;
  /* width: 50vw; */
  /* margin-top: 10vh; */
  margin-bottom: 1vh;
}

.education-item-grid {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-template-rows: auto;
  grid-gap: 10px;
  margin: 10px;
}

.employment-logo {
  width: 100px;
}

.education-item-title {
  padding: 0;
}

.education-item-note-text {
  font-style: italic;
  padding: 0;
}

.education-item-hr {
  width: 90%;
  height: 2px;
  color: #032C59;
  background-color: #032C59;
  border: none;
}



