.work-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-auto-flow: row;
  grid-gap: 2rem;
  width: 50vw;
  margin-top: 15vh;
  margin-bottom: 1vh;
  /* For Collapsed View */
  /* margin-left: 22vw; */

  /* for extended view */
  /* margin-left: 35vw; */
}

.work-logo {
  width: 100px;
}

.work-slider-link {
  color: white;
  font-size: 2rem;
  text-decoration: none;
  font-family: Garamond,Baskerville,Baskerville Old Face,Hoefler Text,Times New Roman,serif;
  font-weight: 600;
  background-color: #032C59;
  padding: 15px;
  border-radius: 5px;
  border-color: white;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.work-slider-link:hover {
  background-color: #CCF;
  color: black;
}

.work-item-hr {
  width: 90%;
  height: 2px;
  color: #032C59;
  background-color: #032C59;
  border: none;
}



