
.employment-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 90%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  /* Collapsed View */
  /* margin-left: 7vw; */
  /* Extended View */
  /* margin-left: 15vw; */
}

.employment-item-container {
  width: 25vw;
  margin-top: 2.5vh;
  margin-bottom: 1vh;
  margin-right: 7vw;
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-template-rows: 1fr;
}

.employment-header {
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
  margin: 0;
}

#employment-h1 {
  font-size: 2vw;
  padding: none;
  margin: none;
}

#employment-h2 {
  font-size: 1vw;
  padding: none;
  margin: none;
}

#employment-time {
  font-size: 0.75vw;
  font-style: italic;
  margin-top: 0;
  font-family: Garamond,Baskerville,Baskerville Old Face,Hoefler Text,Times New Roman,serif;
  font-weight: 600;
}

.employment-hr {
  width: 25vw;
  color: #023670;
  height: 2px;
  background-color: #023670;
}

.employer-logo {
  width: 100px;
  margin-right: 15px;
  border-radius: 5px;
}

.employment-description {
  font-family: Garamond,Baskerville,Baskerville Old Face,Hoefler Text,Times New Roman,serif;
  font-size: 1rem;
}



